*{
  margin: 0;
  padding: 0;
  font-size: 16px;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  background-color: hsl(0, 100%, 74%);
  background-image: url(./image/bg-intro-desktop.png);
  margin: 115px 155px 100px 155px; 
}

.wrapper {
  display: flex;
  flex-direction: column;
}

main {
  display: grid;
  grid-template-columns: 1fr, 1fr;
}

.left {
  color: white;
  grid-area: 1 / 1 / 2 / 2;
  place-self: center;
}

h1 {
  font-size: 48px;
  line-height: 56px;
  margin-bottom: 25px;
}

h1 + p {
  line-height: 26px;
}


.right {
  color: white;
  box-sizing: border-box;
  grid-area: 1 / 2 / 2 / 3;
}

.heading {
  color: white;
  font-weight: 400;
  padding: 15px 50px;
  text-align: center;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 1px 6px rgba(0, 0, 0, 0.25);
  background-color: hsl(248, 32%, 49%);
}

.heading span {
   color: white;
   font-weight: 700;
}

form {
  width: 565px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 1px 7px rgba(0, 0, 0, 0.25);
}

input {
  margin-bottom: 10px;
}

input, button {
  width: 95%;
  padding: 12px 20px;
  border-radius: 7px;
  box-sizing: border-box;
}

button {
  border: none;
  color: white;
  background-color: hsl(154, 59%, 51%);
}

button:hover {
  background-color: hsl(248, 32%, 49%);
}

form p {
  color: hsl(246, 25%, 77%);
  font-size: 10px;
  margin: 10px;
}

form span {
  font-size: 11px;
  color: hsl(0, 100%, 74%);
  font-weight: 700;
}

@media (max-width: 992px) {
 
 body {
  margin: 25px; 
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: hsl(0, 100%, 74%);
  background-image: url(./image/bg-intro-mobile.png);
 }
  

  main {
      display: flex;
      flex-direction: column;
      width: 100%;
  }

  .left {
      margin: 0;
      margin-bottom: 45px;
      text-align: center;
  }

  h1 {
      font-size: 32px;
      font-weight: 600;
      line-height: 36px;
      margin-bottom: 25px;
  }
  
  h1 + p {
      font-size: 16px;
      line-height: 28px;
  }

  .heading {
      padding: 15px 50px;
  }

  .heading span {
      font-weight: 600;
  }

  .right {
      width: 100%;
      display: flex;
      flex-direction: column;
  }

  form {
      width: 100%;
      max-width: 992px;
      display: flex;
      flex-direction: column;
  }

  form p {
      padding: 0 25px;
  }

  form span {
      font-size: 10px;
  }

}
